@mixin heading {
    position: relative;
    padding : 40px 0px;
    a {
        color: $bg;
        text-transform: uppercase;
        letter-spacing: .5rem;
        font-size: 2rem;
        font-weight: 700;
        position: relative;
        text-decoration: none;
        background: $color-white;
        z-index: 9999; }
    @media (max-width:767px) {
        font-size: 1.2rem; }
    &::after {
        content: '';
        display: block;
        position: absolute;
        top: 50%;
        height: 2px;
        background: $bg;
        width: 100%; } }



@-webkit-keyframes ani {
    from {
        -webkit-mask-position: 0;
        mask-position: 0; }
    to {
        -webkit-mask-position: 100% 0;
        mask-position: 100% 0; } }
@keyframes ani {
    from {
        -webkit-mask-position: 0 0;
        mask-position: 0 0; }
    to {
        -webkit-mask-position: 100% 0;
        mask-position: 100% 0; } }

@-webkit-keyframes sdb10 {
    0% {
        -webkit-transform: translate(0, 0);
        opacity: 0; }
    40% {
        opacity: 1; }
    80% {
        -webkit-transform: translate(0, 20px);
        opacity: 0; }
    100% {
      opacity: 0; } }
@keyframes sdb10 {
    0% {
        transform: translate(0, 0);
        opacity: 0; }
    40% {
        opacity: 1; }
    80% {
        transform: translate(0, 20px);
        opacity: 0; }
    100% {
        opacity: 0; } }
@-webkit-keyframes sdb07 {
    0% {
        opacity: 0; }
    50% {
        opacity: 1; }
    100% {
        opacity: 0; } }
@keyframes sdb07 {
    0% {
        opacity: 0; }
    50% {
        opacity: 1; }
    100% {
        opacity: 0; } }




