@import '../_includes';


$color-white : #ffffff;
$color : #404040;
$bg : #1e96e6;




body {
    font-family: 'SVN-Poppins', sans-serif; }

.top-header {
    padding : 10px 0px;
    background: $bg;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999999999999999999999999999;
    .main-menu {
        width: 100%;
        .bg-dark {
            background: transparent !important; }
        .navbar-toggler {
            color:#fff!important {}
            border: none !important;
            outline: none !important; }
        .logo {
            text-decoration: none;
            color: $color-white;
            font-size: 32px; }
        .navbar-collapse {
            justify-content: space-evenly !important;
            ul {
                list-style: none;
                margin-bottom: 0px;
                li {
                    padding: 0px 20px;
                    a {
                        text-decoration: none;
                        color : $color-white;
                        font-size: 18px;
                        text-transform: uppercase;
                        &:after {
                            display: block;
                            content: '';
                            border-bottom: 2px solid $color-white;
                            transform: scale(0);
                            transition: transform 250ms ease-in-out; }
                        &:hover:after {
                            transform: scale(1);
                            @media (max-width:767px) {
                                font-size: 16px; } } } }
                .active {
                    a {
                        color: $color;
                        font-weight: 700; } } }
            .social-menu {
                li {
                    display: inline-block; }
                .facebook,.skype {
                    padding : 0px;
                    a {
                        font-size: 30px; } }
                .skype {
                    padding-left: 15px; } } } } }

.main-block {
    background: #000000;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    .box-intro,.box1,.box2 {
        width: 10vw;
        height: 10vw;
        background: linear-gradient(to left top,$bg,rgba(0,191,251,0));
        position: absolute;
        will-change: transform;
        overflow: hidden;
        transform: rotate(45deg);
        top: 41.9%;
        left: 0;
        &::before {
            content: '';
            display: block;
            background: linear-gradient(270deg,#15181d 0,rgba(21,24,29,0) 60%);
            transform-origin: 100% 100%;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            transform: rotate(45deg) scale(1.5); } }
    .box2 {
        transform: rotate(180deg);
        opacity: 0;
        transition: all .5s ease; }
    .box1 {
        width: 70px;
        height: 70px;
        top: 15%;
        left: 54.9%;
        z-index: 9999;
        transform: rotate(90deg);
        opacity: 0;
        transition: all .5s ease;
        transition-delay: .2s; }
    .box3 {
        left: 87.7%;
        top: 20%;
        width: 20px;
        height: 20px;
        transform: rotate(180deg);
        opacity: 0;
        transition: all .5s ease;
        transition-delay: .6s; }
    .active-box {
        transform: rotate(45deg);
        opacity: 1;
        transition: all .5s ease; }



    .bg-me {
        display: flex;
        align-items: center;
        flex-flow: row wrap;
        position: relative;
        height: 100vh;
        padding: 10px;
        max-width: 1200px;
        margin: 0 auto;
        overflow: hidden;
        .intro-me {
            max-width: 75%;
            position: static;
            z-index: 99;
            @media (max-width:767px) {
                max-width: 100%; }
            h3 {
                font-size: 2.5rem;
                color: $bg;
                text-transform: uppercase;
                font-weight: 700;
                letter-spacing: 1.3px;
                @media (max-width:767px) {
                    font-size: 1.5rem; } }
            h1 {
                font-size: 6rem;
                text-transform: uppercase;
                font-weight: 700;
                color: $color-white;
                text-shadow: 2px 2px black;
                @media (max-width:767px) {
                    font-size: 2.5rem; } } }
        .img-me {
            position: absolute;
            background: url('../../images/me.jpg');
            top: 18%;
            border-radius: 10px 10px 0px 0px;
            right: 0;
            bottom: 0;
            left: 50%;
            background-position: center center;
            background-size: cover;
            background-repeat: no-repeat;
            z-index: 9;
            @media (max-width:767px) {
                left: 20%; } }
        .intro-grid {
            background: url(../../images/pattern-dot-grid.svg);
            background-size: 24px 24px;
            opacity: 1.15;
            position: absolute;
            top: 30%;
            right: 0;
            bottom: 0;
            left: 30%;
            z-index: 0;
            @media (max-width:767px) {
                left: 10%; } }
        .intro-scroll {
            a {
                font-weight: 400;
                font-size: 1.2rem;
                line-height: 2;
                text-transform: uppercase;
                letter-spacing: .3rem;
                position: absolute;
                bottom: 8rem;
                left: 10px;
                z-index: 1;
                text-decoration: none;
                color: $bg;
                z-index: 999;
                font-weight: 700;
                @media (max-width:767px) {
                    font-size: 1rem; }
                &::after {
                    content: '';
                    display: block;
                    top: 50%;
                    right: -80px;
                    position: absolute;
                    height: 2px;
                    width: 72px;
                    background: $bg; } } }


        .down {
            position: absolute;
            bottom: 10%;
            right: 50%;
            transform: translate(8%,-50%);
            z-index: 9999999;
            cursor: pointer;
            .mouse {
                span {
                    position: absolute;
                    top: 0;
                    left: 80%;
                    width: 20px;
                    height: 35px;
                    margin-left: -13px;
                    border: 2px solid  $bg;
                    background: $bg;
                    border-radius: 50px;
                    box-sizing: border-box;
                    box-shadow: 0 0 4px $bg;
                    &::before {
                        position: absolute;
                        top: 2px;
                        left: 50%;
                        content: '';
                        width: 7px;
                        height: 7px;
                        margin-left: -3px;
                        background : transparent;
                        border-radius: 100%;
                        -webkit-animation: sdb10 2s infinite;
                        animation: sdb10 2s infinite;
                        box-sizing: border-box;
                        border: 2px solid #000; } } }
            .scroll-down {
                position: relative;
                z-index: 9999;
                span {
                    position: absolute;
                    top: 40px;
                    left: 80%;
                    width: 10px;
                    height: 10px;
                    margin-left: -8px;
                    border-left: 1px solid $bg;
                    border-bottom: 1px solid $bg;
                    -webkit-transform: rotate(-45deg);
                    transform: rotate(-45deg);
                    -webkit-animation: sdb07 2s infinite;
                    animation: sdb07 2s infinite;
                    opacity: 0;
                    box-sizing: border-box;
                    &:nth-of-type(1) {
                        -webkit-animation-delay: 0s;
                        animation-delay: 0s; }
                    &:nth-of-type(2) {
                        top: 47px;
                        -webkit-animation-delay: .15s;
                        animation-delay: .15s; }
                    &:nth-of-type(3) {
                        top: 54px;
                        -webkit-animation-delay: .3s;
                        animation-delay: .3s; } } } } } }

.main-content {
    padding-top: 4rem;
    .block-content {
        padding: 0 0 4rem 0;
        .heading-block {
            @include heading; }
        .main-about-me {
            p {
                font-weight: 500;
                font-size: 1.3rem;
                line-height: 1.8; }
            .btn-download {
                max-width: 350px;
                text-align: center;
                margin: 40px 0px;
                a {
                    text-decoration: none;
                    display: block;
                    background: $bg;
                    color: $color-white;
                    font-size: 1.2rem;
                    letter-spacing: .3rem;
                    padding: 10px 30px;
                    border-radius: 5px;
                    box-shadow: 0 5px 30px 0 #a71ee680; } } } }
    .block-skills {
        padding: 6rem 0;
        background: url('../../images/bg.png');
        background-attachment: fixed;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        .overlay {
            position: absolute;
            background: rgba(0,0 ,0 ,0.7 );
            top: 0;
            width: 100%;
            height: 100%; }
        .heading-block {
            text-align: center;
            @include heading;
            a {
                background: transparent !important; }
            &::after {
                width: 135px;
                top: 75%;
                right: 50%;
                transform: translate(50%,-50%); } }
        .wapperr {
            max-width: 800px;
            margin: 0 auto;
            .skill {
                margin-bottom: 35px;
                position: relative;
                overflow: hidden;
                width: 100%;
                p {
                    font-size: 18px;
                    font-weight: 700;
                    color: $color-white;
                    margin: 5px 0px; }
                &:before {
                    width: 100%;
                    height: 15px;
                    content: '';
                    display: block;
                    position: absolute;
                    background: #dddddd;
                    border-radius: 5px;
                    bottom: 0; }
                .skill-bar {
                    width: 100%;
                    height: 15px;
                    background: $bg;
                    transition: all .3s ease;
                    display: block;
                    position: relative;
                    border-radius: 5px;
                    span {
                        position: absolute;
                        top: -30px;
                        padding: 0px;
                        font-size: 18px;
                        padding: 3px 0;
                        font-weight: 500;
                        color: $color-white; } }
                .skill1 {
                    width: 95%;
                    .skill-count1 {
                        right: 0px; } }
                .skill2 {
                    width: 90%;
                    .skill-count2 {
                        right: 0px; } }
                .skill3 {
                    width: 70%;
                    .skill-count3 {
                        right: 0px; } }
                .skill4 {
                    width: 75%;
                    .skill-count4 {
                        right: 0px; } }
                .skill5 {
                    width: 65%;
                    .skill-count5 {
                        right: 0px; } }
                .skill6 {
                    width: 80%;
                    .skill-count6 {
                        right: 0px; } }
                .skill7 {
                    width: 50%;
                    .skill-count7 {
                        right: 0px; } } } } }
    .block-experience {
        padding: 6rem 0;
        .heading-block {
            @include heading; }
        .timeline {
            position: relative;
            margin: 50px auto;
            padding: 40px 0;
            width: 1000px;
            box-sizing: border-box;
            @media (max-width:1000px) {
                width: 100%; }
            ul {
                padding: 0;
                margin: 0;
                li {
                    list-style: none;
                    position: relative;
                    width: 50%;
                    padding: 20px 40px;
                    box-sizing: border-box;
                    &:nth-child(odd) {
                        float: left;
                        text-align: right;
                        clear: both;
                        @media (max-width:767px) {
                            width: 100%;
                            text-align: left;
                            padding-left: 50px;
                            padding-bottom: 50px; }
                        &::before {
                            content: '';
                            position: absolute;
                            width: 10px;
                            height: 10px;
                            top: 24px;
                            right: -6px;
                            background: $bg;
                            border-radius: 50%;
                            box-shadow: 0 0 0 3px #b3d7ff;
                            @media (max-width:767px) {
                                top: -18px;
                                left: 16px; } }
                        .time {
                            position: absolute;
                            top: 12px;
                            right: -165px;
                            margin: 0;
                            padding: 8px 16px;
                            background: $bg;
                            color: #fff;
                            border-radius: 18px;
                            box-shadow: 0 0 0 3px #b3d7ff;
                            @media (max-width:767px) {
                                top: -30px;
                                left: 50px;
                                right: inherit; } } }
                    &:nth-child(even) {
                        float: right;
                        text-align: left;
                        clear: both;
                        @media (max-width:767px) {
                            width: 100%;
                            text-align: left;
                            padding-left: 50px;
                            padding-bottom: 50px; }
                        &::before {
                            content: '';
                            position: absolute;
                            width: 10px;
                            height: 10px;
                            top: 24px;
                            left: -4px;
                            background: $bg;
                            border-radius: 50%;
                            box-shadow: 0 0 0 3px #b3d7ff;
                            @media (max-width:767px) {
                                top: -18px;
                                left: 16px; } }
                        .time {
                            position: absolute;
                            top: 12px;
                            left: -165px;
                            margin: 0;
                            padding: 8px 16px;
                            background: $bg;
                            color: #fff;
                            border-radius: 18px;
                            box-shadow: 0 0 0 3px #b3d7ff;
                            @media (max-width:767px) {
                                top: -30px;
                                left: 50px;
                                right: inherit; } } }
                    .content {
                        padding-bottom: 20px;
                        h3 {
                            padding: 0;
                            margin: 0;
                            color: $bg;
                            font-weight: 600;
                            font-size: 20px;
                            line-height: 1.5; }

                        p {
                            margin: 10px 0 0;
                            padding: 0;
                            line-height: 1.7;
                            font-size: 1.2rem; } }
                    .time {
                        h4 {
                            margin: 0;
                            padding: 0;
                            font-size: 14px; } } } }
            &::before {
                content: '';
                position: absolute;
                left: 50%;
                width: 2px;
                height: 100%;
                background:#c5c5c5 {}
                @media (max-width:767px) {
                    left: 20px;
                    height: 100%; } } } }







    .block-project {
        padding: 6rem 0;
        .heading-block {
            @include heading; }
        .item-projects {
            min-height: 500px;
            height: 500px;
            overflow: hidden;
            position: relative;
            border-radius: 10px;
            box-shadow: 0 10px 20px 0 rgba(0,0,0,0.05);
            cursor: pointer;
            .title-projects {
                background-image: linear-gradient(to bottom, transparent, #000);
                padding: 25px 15px 15px 15px;
                position: absolute;
                bottom: 0;
                width: 100%;
                overflow: hidden;
                margin: 0;
                z-index: 999;
                color: $color-white;
                text-align: center; }
            .overlay-projects {
                width: 100%;
                height: 100%;
                position: absolute;
                background: $bg;
                top: 0;
                left: 0;
                opacity: 0;
                transition: all .3s ease;
                transform: translateY(-500px); }
            &:hover {
                .overlay-projects {
                    opacity: 0.6;
                    transition: all .3s ease;
                    transform: translateY(0px); }
                .more {
                    opacity: 1;
                    transition: all .3s ease; } }
            .more {
                position: absolute;
                top: 50%;
                right: 50%;
                transform: translate(50%,-50%);
                opacity: 0;
                transition: all .3s ease; } }
        .next {
            color: $bg;
            font-size: 1.5rem; } }

    #contacts {
        background: #000000;
        padding: 15rem 0;
        @media (max-width:767px) {
            padding: 5rem 0; }
        .main-contacts {
            .content,.content1 {
                margin: 60px 0px;
                font-size: 1.2rem;
                color: $color-white;
                a {
                    text-decoration: underline;
                    font-size: 1.3rem;
                    color: $bg;
                    @media (max-width:767px) {
                        font-size: 1.1rem; } } }
            .content1 {
                font-size: 2rem;
                @media (max-width:767px) {
                    font-size: 1.3rem; } }
            .phone {
                font-size: 1.5rem;
                color: $color-white;
                a {
                    font-size: 2rem;
                    color: $bg;
                    text-decoration: none;
                    @media (max-width:767px) {
                        font-size: 1.3rem; } } }
            .thanks {
                font-size: 1.5rem;
                color: $bg;
                @media (max-width:767px) {
                    font-size: 1.3rem; } } } }
    .copyright {
        background: $bg;
        padding: 2rem 0;
        .by-me {
            font-size: 1.1rem;
            font-weight: 700; } }

















