@media (max-width:767px) {
    .top-header {
        .main-menu {
            .navbar-nav {
                margin-top: 20px;
                li {
                    padding: 0px !important;
                    margin: 10px 0px; } }
            .logo {
                font-size: 22px; }

            .social-menu {
                .facebook {
                    margin-top: 10px !important; } } } } }
@media (min-width :768px) and (max-width:991px) {
    .top-header {
        .main-menu {
            .navbar-nav {
                margin-top: 20px;
                li {
                    padding: 0px !important;
                    margin: 10px 0px; } }
            .social-menu {
                .facebook {
                    margin-top: 10px !important; } } } } }
